.contentWrap {
    margin: 0 auto;
    max-width: 1270px;
}

.serviceSection {
    background-color: #0a0e0e;
    padding-top: 130px;
    padding-bottom: 100px;
    filter: grayscale(100%);
    background-repeat: no-repeat;
    background-size: cover;
}

.serviceSection .smallDescription {
    margin: 22px 0 57px;
}

.priceList {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 60px;
}

.priceList .item {
    border: 1px solid #ffffff;
    border-radius: 2px;
    padding: 40px 48px 112px;
    min-width: 530px;
    background-color: #0A0E0E;
}

.priceList .item h6 {
    font-weight: 700;
    font-size: 32px;
    line-height: 30px;
    color: #ffffff;
    text-align: center;
    margin-bottom: 40px;
}

.priceList .item .list {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.priceList .item .list p,
.priceList .item .list label {
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
    color: #ffffff;
    margin-bottom: 24px;
}

.priceList .item .list label {
    font-weight: 400;
}

.serviceSection .bottomBlock {
    border: 1px solid #ffffff;
    border-radius: 2px;
    padding: 43px 48px 42px;
    background-color: #0A0E0E;
}

.serviceSection .bottomBlock h6 {
    font-weight: 700;
    font-size: 32px;
    line-height: 30px;
    color: #ffffff;
    text-align: center;
}

@media (max-width: 1200px) {
    .priceList .item {
        min-width: 470px;
    }
}

@media (max-width: 991px) {
    .priceList .item {
        width: 100%;
        min-width: auto;
        margin-bottom: 20px;
    }
}

@media (max-width: 575px) {
    .serviceSection .bottomBlock h6 {
        font-size: 22px;
    }
}